export default {
  ctrlCaption: "流程信息",
  props: {
    caption: {
      type: String,
      default() {
        return "wfinfo";
      },
    },
    chooseFlow: {
      type: Object,
      default() {
        return {};
      },
    },
    eventName: {
      type: String,
      default() {
        return "";
      },
    },
    countDown: {
      type: String,
      default() {
        return "3";
      },
    },
    linkData: {
      type: Array,
      default() {
        return [];
      },
    },
    forceHidden: {
      type: Boolean,
      default: true
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("flow-info-meta-data");
      },
    },
  },
  data() {
    return {
      _RequestID: null,
      flowSendOverEvent: "",
      owner: null,
      dialog: null,
      isFristSend: false,
      BackLineID: "",
    };
  },
  watch: {
    chooseFlow(val) {
      const value = {
        wf_id: val.ID,
        wf_name: val.name,
      };
      this.emitValueChange(value);
    },
  },
  computed: {},
  mounted() {
    if (this.$vm && this.$vm.flows) {
      this.$vm.flows.flowInfo = this;
    }
  },
  destroyed() { },
  methods: {
    linkSetChooseFlow() {
      document.querySelector("[a='linkData2'] .ds-button").click();
    },
    flowSend(res, { line, flowSendOverEvent, completed }, owner, Objects) {
      this.dialog = null;
      this.flowSendOverEvent = flowSendOverEvent;
      this.owner = owner;
      // 流程发送按钮
      let id = this.chooseFlow.dsfa_rm_id;
      if (id) {
        // 增加 sExInfos
        let sExInfos = Object.assign({}, dsf.removePoint(this.$vm.$viewData), this.$route.query);
        // 开始发送
        let url = "wfr/startSend",
          param = {
            sWFID: id,
            sObjectID: res._id,
            sObjectName: this.$vm.$refs[this.eventName]?.value || "",
            sCurNodeID: "",
            sExInfos: JSON.stringify(sExInfos),
          };

        //继续发送
        if (Objects || this.$route.query.sPID || this.queryString.sPID) {
          url = "wfr/send";
          param = {
            sPID: Objects?.sPID || this.$route.query?.sPID || this.queryString?.sPID,
            sCurLinkID: Objects?.sCurLinkID || this.$route.query?.sCurLinkID || this.queryString?.sCurLinkID,
            sExInfos: JSON.stringify(sExInfos),
          };
          this.isFristSend = false;
        } else {
          this.isFristSend = true;
        }
        // 发送时有处理意见
        if (this.$vm?.flows?.disposeIdea?.sOpinion) {
          param.sOpinion = JSON.stringify(this.$vm.flows.disposeIdea.sOpinion);
        }
        // 发送时表单意见框有处理意见
        if (this.$vm?.flows?.sOpinion?.Content || this.$vm?.flows?.sOpinion?.Attachments?.length > 0) {
          param.sOpinion = JSON.stringify(this.$vm.flows.sOpinion);
        }
        if (line) {
          param.sLineIDs = line.LineID;
        }

        const config = {
          param: param,
          url: url,
        };

        this.$http
          .post(config.url, config.param)
          .then((response) => {
            this.handler(response, false, completed);
          })
          .catch((response) => {
            console.error(response);
            dsf.layer.message("发送异常", false);
            this._RequestID = null;
          });
      } else {
        dsf.layer.message("请先配置流程信息控件的'选择流程'", false);
      }
    },
    handler(response, isShowBack, completed) {
      if (this.dialog) {
        this.dialog.close();
        this.dialog = null;
      }
      if (response.data.state == 20000) {
        let data = response.data.data;
        this._RequestID = data.RequestID;
        if (data.Code == 20000 || data.Code == 20001 || data.Code == 20003 || data.Code == 20004 || data.Code == 20005) {
          // 发送成功 || 发送到汇总节点 || 发送到汇总环节并激活汇总环节 || 办结当前节点 || 办结当前节点，并办结整个流程
          if (this.flowSendOverEvent) {
            this.owner.$dispatch(this.flowSendOverEvent);
          } else {
            this.directlySend(data, completed);
          }
        } else if (data.Code == 81000) {
          // 或选择
          this.chooseSendNode(data);
        } else if (data.Code == 82000) {
          // 与选择
          this.chooseSendTree(data, isShowBack);
        } else {
          let msg = data.Message;
          if (data.EsMessage) {
            msg = msg + "." + data.EsMessage;
          }
          dsf.layer.message(msg || "未知流程类型发送", false);
        }
      } else {
        this._RequestID = null;
        let msg = data.Message;
        if (data.EsMessage) {
          msg = msg + "." + data.EsMessage;
        }
        dsf.layer.message(msg || "发送异常", false);
      }
    },
    directlySend(data, completed) {
      let list = [],
        _this = this;
      if (data.Code == 20000) {
        data.Links.forEach((line, index) => {
          if (line.Status == 0 && line.LinkUser) {
            let linkUser = line.LinkUser;
            let type = line.LinkUser.Type;
            if (type == 1) {
              // 直接选人
              list.push(linkUser.UserName);
            } else if (type == 2) {
              let GlobalDeptName = linkUser.GlobalDeptName.split(",");
              let DeptName = GlobalDeptName.length >= 3 ? GlobalDeptName[2] : linkUser.DeptName;
              // 部门+角色
              list.push(`${DeptName}-${linkUser.RoleName}`);
            } else if (type == 3) {
              // 岗位
              list.push(linkUser.JobName);
            } else if (type == 4) {
              // 人+关系
              list.push(`${linkUser.UserName}-${linkUser.RelationName}`);
            } else if (type == 5) {
              // 机器人
              list.push(linkUser.RobotName);
            }
          }
        });
        this.$openDialog({
          type: "dialog",
          title: "流程已发送到以下人员：",
          width: "300px",
          height: "auto",
          overflow: "auto",
          content: "SendFlowDialog",
          footerAlign: "center",
          showClose: false,
          params: {
            list: _.uniq(list),
            countDownTime: this.countDown,
          },
          btns: [
            {
              text: "确定",
              class: `sendFlow ${window.$isMobile ? "van-dialog__confirm" : ""}`,
              handler: (component) => {
                this.closeOpener();
                return true;
              },
            },
          ],
        });
      } else {
        let msg = "发送成功",
          code = data.Code;
        switch (code) {
        case 20004:
          msg = completed || "文件已办结";
          break;
        case 20005:
          msg = completed || "文件已办结";
          break;
        }
        this.$openDialog({
          type: "dialog",
          title: "提示",
          width: "300px",
          height: "120px",
          content: "SendFlowDialog",
          footerAlign: "center",
          showClose: false,
          params: {
            msg: msg,
            countDownTime: this.countDown,
          },
          btns: [
            {
              text: "确定",
              class: `sendFlow ${window.$isMobile ? "van-dialog__confirm" : ""}`,
              handler: (component) => {
                this.closeOpener();
                return true;
              },
            },
          ],
        });
      }
    },
    getSendBackLink(args) {
      let param = {
        sPID: args[0] || null,
        sCurLinkID: args[1] || null,
      };
      this.$http
        .post("wfr/getSendBackLink", param)
        .then((res) => {
          if (res.data.success && res.data.data && res.data.data.BackLinks) {
            let BackLinks = res.data.data.BackLinks;
            if (BackLinks.length == 1) {
              this.sendBack(args, BackLinks[0].ID);
            } else {
              this.chooseBackNode(BackLinks, args);
            }
          } else {
            dsf.layer.message(res.data.data.Message || "退回异常", false);
          }
        })
        .catch((res) => {
          dsf.layer.message("退回异常", false);
        })
        .finally(() => {
          // dsf.layer.close(loadingIndex);
        });
    },
    sendBack(args, id) {
      dsf.layer
        .prompt("", "退回原因", {
          inputPlaceholder: "请输入退回原因",
          inputType: "textarea",
          inputPattern: /\S/,
          inputErrorMessage: "退回原因不能为空",
          confirmButtonText: "确定",
        })
        .then(({ value }) => {
          let param = {
            sPID: args[0] || null,
            sCurLinkID: args[1] || null,
            sReason: value || null,
            sBackLinkID: id,
          };
          this.$http
            .post("wfr/sendBack", param)
            .then((res) => {
              if (res.data.success && res.data.data && res.data.data.Code == "21000") {
                dsf.layer.message("退回成功", true);
                this.closeOpener();
              } else {
                dsf.layer.message(res.data.Message || "退回异常", false);
              }
            })
            .catch((res) => {
              dsf.layer.message("退回异常", false);
            });
        });
    },
    // 选择退回节点
    chooseBackNode(data, args) {
      this.$openDialog({
        type: "dialog",
        title: "请选择要退回的环节：",
        width: "300px",
        height: "auto",
        overflow: "auto",
        footerAlign: "center",
        content: (window.$isMobile ? "DsfMobile" : "") + "ChooseSendNode",
        params: {
          lines: data,
          callback: this.sendBack,
          requestID: this._RequestID,
          type: "back",
        },
        btns: [
          // {
          //   text: "确定",
          //   class: `hide ${window.$isMobile ? "van-dialog__confirm" : ""}`,
          //   handler: (component) => {
          //     if (component.validate()) {
          //       const line = component.yes();
          //       this.sendBack(args);
          //       return true;
          //     }
          //     return false;
          //   },
          // },
          {
            class: "close-send-node",
            text: "取消",
            handler: () => {
              this.dialog = null;
              return true;
            },
          },
        ],
      });
    },
    chooseSendNode(data) {
      this.dialog = this.$openDialog({
        type: "dialog",
        title: "请选择要发送的环节：",
        width: "300px",
        height: "auto",
        overflow: "auto",
        footerAlign: "center",
        content: (window.$isMobile ? "DsfMobile" : "") + "ChooseSendNode",
        params: {
          lines: data.SendParams.Lines,
          callback: this.handler,
          requestID: this._RequestID,
          type: "next",
        },
        btns: [
          {
            text: "返回",
            class: `${data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].SendToLineID ? "" : "hide"}`,
            handler: (component) => {
              let param = {
                sRequestID: this._RequestID,
                sLineID: data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].BackLineID,
              };
              this.$http.post("wfr/setLineToBack", param).then((res) => {
                if (res.data.state == 20000) {
                  this.handler(res);
                }
              });
              if (this.dialog) {
                this.dialog.close();
              }
              return true;
            },
          },
          {
            text: "确定",
            class: "hide",
            handler: (component) => {
              if (component.validate()) {
                const line = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sLineID: line.LineID,
                };
                this.$http.post("wfr/sendToLine", param).then((res) => {
                  if (res.data.state == 20000) {
                    this.handler(res);
                  } else {
                    dsf.layer.message(res.data.Message || "发送异常", false);
                  }
                });
                return true;
              }
              return false;
            },
          },
          {
            class: "close-send-node",
            text: "取消",
            handler: () => {
              this.dialog = null;
              return true;
            },
          },
        ],
      });
    },
    chooseSendTree(data, isShowBack) {
      this.dialog = this.$openDialog({
        title: "请选择办理人",
        width: "900px",
        height: "420px",
        overflow: "hidden",
        content: "ChooseSendTree",
        params: {
          lines: data.SendParams.Lines,
        },
        btns: [
          {
            text: "返回",
            class: `choose-send-node ${isShowBack ? "" : "hide"}`,
            handler: (component) => {
              let param = {
                sRequestID: this._RequestID,
                sLineID: data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].SendToLineID,
              };
              this.$http.post("wfr/setLineToBack", param).then((res) => {
                if (res.data.state == 20000) {
                  this.handler(res);
                }
              });
              if (this.dialog) {
                this.dialog.close();
              }
              return true;
            },
          },
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const result = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sReceivers: result,
                };
                this.$http.post("wfr/sendToReceivers", param).then((res) => {
                  if (res.data.state == 20000) {
                    this.handler(res);
                  }
                });
                const closebtn = document.querySelector(".close-send-node");
                closebtn && closebtn.click();
                return true;
              }
              return false;
            },
          },
          {
            text: "取消",
            handler: () => {
              this.dialog = null;
              return true;
            },
          },
        ],
      });
    },
    closeOpener() {
      if (window.$isMobile) {
        if (window.sendFlowTimer) {
          clearTimeout(window.sendFlowTimer)
          delete window.sendFlowTimer
        }
        this.$router.go(-1);
        return true;
      }
      if (this?.$vm?.$dialog) {
        this.$vm.$dialog?.openerVm?.reloadData?.(true);
        this.$vm.$dialog?.close?.();
      } else if (this?.$vm?.$openerVm) {
        this.$vm.$openerVm?.reloadData?.(true);
        this.$vm?.$closeWindow?.();
      } else if (window?.opener) {
        window?.opener?.reloadData?.(true);
        window.close();
      }
    }
  },
};
