// let rxp_viewdata = /^\$viewData(\.|\[.+\])/g
let form = {
  provide() {
    return {
      $form: this
    };
  },
  props: {
    nameSpace: {
      type: String,
      default: ""
    },
    pageName: {
      type: String,
      default: ""
    },
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      _isForm: true,
      postFields: [],
      subTables: [],
      loading: false,
      isView: false,
      ignoreValidate: false,
      validating: false,
      _errors: []
    };
  },
  computed: {
    $errors() {
      return this.$data._errors;
    },
    $isForm() {
      return this.$data._isForm;
    }
  },
  beforeCreate() {
  },
  created() {
    this.$data.$forceReadOnly = this.queryString.isview == 1;
  },
  dataLoaded(data, next) {
    next();
  },

  beforeDestroy() {
    for (let k in this.metaDataAndCtrls) {
      delete this.metaDataAndCtrls[k];
    }
    this.metaDataAndCtrls = null;
  },
  methods: {
    //调用数据加载接口
    async $loadData() {
      if (this.$dataKey) {
        let params = {
          ...this.queryString,
          pageName: this.pageName,
          namespace: this.nameSpace,
          data: JSON.stringify({
            _id: this.$dataKey
          })
        };
        try {
          let result = await this.$webAPI.getFormData(params, true);
          return result.data;
        } catch (err) {
          throw err;
          // return {
          //   message: err.message || err
          // };
        }
      }
    },
    //表单提交操作后的更新
    async $updateData() {
      try {
        this.loading = true;
        let viewData = {};
        let initData = await this.$initData();
        dsf.mix(this.context.$$viewInitData, initData.data);
        viewData = this.$initControls(this);
        let data = await this.$loadData();
        data = dsf.mix(viewData, data?.data || null);
        this.context.$$viewData = data;
        if (this.$options.updateData) {
          await promiseFn.call(this, this.$options.updateData, [opt]);
        }
      }
      catch (error) {
        dsf.httpError(error);
      }
      finally {
        this.loading = false;
      }
    },
    //表单验证（不触发验证前后的钩子）
    async validate(vdatakey, index) {
      let arrs = [];
      let dict = this.metadataDict;
      let allKeys = this.metadataKeyList;
      let keys = [];
      //全局验证
      if (arguments.length == 0) {
        keys = _.filter(this.metadataKeyList, (it) => {
          if (!dict[it].inSubTable) {
            return true
          }
        })
      }
      else {
        keys = (dsf.type(vdatakey) == 'array' ? vdatakey : [vdatakey])
      }
      for (let i = 0; i < keys.length; i++) {
        let data = this.$viewData;
        let key = keys[i];
        let meta = dict[key];
        if (!meta?.refs?.length) {
          continue;
        }
        //如果是整个子表
        if (meta.type == 'subtable') {
          let list = data[key] || [];
          let subKeys=[];
          _.each(dict,(it,itKey)=>{
            if(it.inSubTable==key){
              subKeys.push(itKey)
            }
          })
          // let subKeys = _.map(sub, (it) => {
          //   return it.indexOf(key + ".") == 0
          // });
          if (dsf.isDef(index)) {
            list = [list[index]]
          }
          _.each(list, (row) => {
            _.each(subKeys, (it) => {
              let formName = it + "-" + row.$hash;
              let subValue = dsf.isDef(row[it]) ? row[it] : null;
              let submeta = dict[it];
              let subkey = it;
              if (dsf.type(subValue) == 'string' && submeta.valueIsHtml) {
                subValue = this.$getEditorConent(subValue);
              }
              if (submeta.validateRule) {
                subkey = submeta.validateRule;
              }
              let disabled = this.$getValidateIsDisabledFn(subkey);
              if (disabled(subValue, row) !== true) {
                let validateFn = this.$getValidateFunction(subkey, dict, formName);
                let fn = validateFn ? validateFn(subValue, row, formName, submeta) : null;
                fn && arrs.push(fn);
              }
            });
          })

        }
        else {
          //具体每个字段
          let formName = key;
          //如果是子表中的字段
          if (meta.inSubTable && dsf.isDef(index)) {
            data = this.$viewData[meta.inSubTable][index];
            formName = formName + "-" + data.$hash;
          }
          let val = dsf.isDef(data[key]) ? data[key] : null;
          if (dsf.type(val) == 'string' && meta.valueIsHtml) {
            val = this.$getEditorConent(val);
          }
          if (meta.validateRule) {
            key = meta.validateRule;
          }

          let disabled = this.$getValidateIsDisabledFn(key);
          if (disabled(val, data) !== true) {
            let validateFn = this.$getValidateFunction(key, dict, formName);
            let fn = validateFn ? validateFn(val, data, formName, meta) : null;
            fn && arrs.push(fn);
          }
        }
      }
      let errors = await this.$execValidateFns(arrs);
      // console.log(errors)
      return errors;
    },
    //表单验证（触发验证前后的钩子）
    async formValidate(opt) {
      this.validating = true
      let validateBefore = this.$options.validateBefore;
      //表单验证后触发
      let validateAfter = this.$options.validateAfter;
      try {
        if (validateBefore) {
          await promiseFn.call(this, validateBefore, [opt]);
        }
        if (!this.ignoreValidate) {
          //执行验证函数
          await this.validate();
        }
        this.clearErrors();
      } catch (err) {
        if (dsf.type(err) == "array") {
          this.pushError(err);
          dsf.warn("表单数据不符合规范");
          return err;
        } else if (dsf.type(err) == "error" || err === false) {
          throw err;
        }
      } finally {
        if (validateAfter) {
          await promiseFn.call(this, validateAfter, [opt]);
          this.validating = false
        }
      }
    },

    //验证组件
    async validateField(value, index, field) {
      let errors = [];
      if (!field) {
        return;
      }
      try {
        if (!field.isOneToMany) {
          //主表或者子表中单个表单组件验证
          this.removeErrorsByTarget(field.$formName);
          await this.validate(field.formName, index);
        }
        else {
          //如果是子表整表或者指定行验证
          let subTableKey = field.formName;
          let subKeys = _.filter(this.metadataKeyList, (it) => {
            return this.metadataDict[it].inSubTable == subTableKey;
          });
          let rows = value;
          //如果指定了行，则使用行验证
          if (dsf.isDef(index)) {
            rows = [value[index]];
          }
          _.each(rows, (row) => {
            this.removeErrorsByRowHash(row.$hash);
          })
          await this.validate(field.formName, index);
        }
        return [];
      }
      catch (err) {
        if (dsf.type(err) == "array") {
          this.pushError(err);
        }
        return err;
      }
    },
    //重置表单验证结果
    resetValidate() {
      var fields = this.postFields;
      this.clearErrors();
      _.each(fields, (field) => {
        field.errors = [];
      });
    },
    //添加表单组件
    $addFiled(ctrl) {
      if (!ctrl.isOneToMany) {
        if (ctrl.isFormControl) {
          this.postFields.push(ctrl);
        }
      } else {
        this.subTables.push(ctrl);
      }
      this.$nextTick(() => {
        this.updateMetaDataDict(ctrl);
        // this.bindMetaData(ctrl);
      });
    },
    //表单组件被销毁
    $removeFiled(ctrl) {
      if (ctrl && !ctrl.isOneToMany) {
        if (ctrl.isFormControl) {
          dsf.array.remove(this.postFields, ctrl);
        } else if (ctrl.isDynamicForm) {
          dsf.array.remove(this.dynamicForms, ctrl);
        }
      } else if (ctrl && ctrl.isOneToMany) {
        dsf.array.remove(this.subTables, ctrl);
      }
      if (this.metadataDict[ctrl.formName]) {
        //refs用来存储组件实例，组件移除时将refs中的实例也移除掉
        dsf.array.remove(this.metadataDict[ctrl.formName].refs || [], ctrl)
        // console.log(ctrl.formName,this.metadataDict[ctrl.formName].refs)
      }
    },
    // 更新metadataDict，因为页面中有存在动态创建的组件（子表，动态布局）
    updateMetaDataDict(ctrl) {
      if (this.metadataDict) {
        let formName = ctrl.formName;
        let fullCode = ctrl.metadata_fullcode;
        let metadata = ctrl.metadata;
        if (!metadata || !formName) {
          return;
        }
        let arr = formName.split(".");
        let prefix = arr[0];
        let name = arr[1] ? arr[1] : "";
        let isUser = false;
        //子表
        if (ctrl.isOneToMany) {
          if (!this.metadataDict[formName]) {
            this.metadataDict[formName] = {
              type: "subtable",
              fieldsLength: 0,
              valueAttributes: [],
              level: 1,
              refs: []
            };
            isUser = true;
          }
          //refs用来存储组件实例，表单验证时可以判断refs中是否存在组件，如果为空数组则表示该key下没有可以验证的组件
          this.metadataDict[formName].refs = this.metadataDict[formName].refs || [];
          this.metadataDict[formName].valueIsHtml = ctrl.valueIsHtml === true ? true : false
          this.metadataDict[formName].validate = metadata.validate;
          this.metadataDict[formName].refs.push(ctrl);
          this.metadataDict[formName].allowPost = ctrl.allowPost === false ? false : true
          dsf.array.ensure(this.metadataKeyList, formName);
        }
        else {
          //主表或者子表内的表单组件
          if (!this.metadataDict[formName]) {
            this.metadataDict[formName] = {
              type: "field",
              fieldsLength: metadata.valueAttributes && metadata.valueAttributes.length,
              level: 0,
              valueAttributes: _.map(metadata.valueAttributes, "code"),
              refs: []
            };
            isUser = true;
          }
          if (ctrl.$list && this.metadataDict[ctrl.$list.formName]) {
            this.metadataDict[formName].level = 1;
            this.metadataDict[formName].inSubTable = ctrl.$list.formName;
          }
          this.metadataDict[formName].refs = this.metadataDict[formName].refs || [];
          this.metadataDict[formName].valueIsHtml = ctrl.valueIsHtml === true ? true : false
          this.metadataDict[formName].validate = metadata.validate;
          this.metadataDict[formName].refs.push(ctrl);
          this.metadataDict[formName].allowPost = ctrl.allowPost === false ? false : true
          dsf.array.ensure(this.metadataKeyList, formName);
        }
        if (this.metadataDict[formName]) {
          this.metadataDict[formName].isUser = isUser;
          if (ctrl.validateRule) {
            this.metadataDict[formName].validateRule = ctrl.validateRule;
          }
        }
      }
    },

    //轮询调用所有组建的postBefore
    $postBefore() {
      let arr = [];
      _.each(this.postFields, (field) => {
        let fn = field.postBefore;
        if (fn) {
          arr.push(fn());
        }
      });
      return Promise.all(arr);
    },
    //轮询调用所有组建的postAfter
    $postAfter() {
      let arr = [];
      _.each(this.postFields, (field) => {
        let fn = field.postAfter;
        if (fn) {
          arr.push(fn());
        }
      });
      return Promise.all(arr);
    },
    //轮询调用所有组建的postError
    $postError() {
      let arr = [];
      _.each(this.postFields, (field) => {
        let fn = field.postError;
        if (fn) {
          arr.push(fn());
        }
      });
      return Promise.all(arr);
    },
    //表单保存
    async formSave(options, afterCallback, isSyncSave) {
      let isAddNew = false;
      if (!this.$dataKey) {
        isAddNew = true;
      }
      options = options || {};
      let opt = {
        isRefresh: false,
        savedClose: false,
        successMsg: "保存成功",
        errorMsg: "保存失败",
        isSilent: false
      };
      opt = dsf.mix(opt, options);
      //如果当前页面为内嵌视图且属性同步保存为true时
      // if (isSyncSave != false && this.$frame && this.$frame.syncSave && this.$isForm) {
      //   if (this.$parentView.$isForm) {
      //     let saveResult = await this.$parentView.formSave(dsf.mix({}, opt, { isSilent: true }));
      //     if (saveResult) {
      //       _.each(this.$parentView.frames, (part) => {
      //         part.$refs.params.id = saveResult._id;
      //         if (part.$refs.view != this) {
      //           let pamas = dsf.mix({}, opt, { isSilent: true });
      //           part.$refs.view.formSave(pamas, false);
      //         } else {
      //           part.$refs.view.formSave(opt, false);
      //         }
      //       });
      //     }
      //     return;
      //   }
      // }
      // let currentDataKey = this.$dataKey;
      let result = await this.$formSave(opt);
      if (result?.data?._id) {
        if (afterCallback && dsf.isFunction(afterCallback)) {
          await afterCallback(result);
        }
        if (opt.savedClose) {
          this.loading = true;
          this.$dataKey = result.data._id;
          window.setTimeout(() => {
            this.loading = false;
            this.$closeWindow && this.$closeWindow();
          }, 2000);
        }
        else {
          this.$dataKey = result.data._id;
          // await this.$updateData();
          for (let k in result.data) {
            this.$set(this.$viewData, k, result.data[k]);
          }
          this.$updateUrl(result.data);
        }
      }
      return result;
    },
    //表单保存私有方法
    async $formSave(options) {
      this.loading = true;
      let saveResult = null;
      let saveBefore = this.$options.saveBefore;
      let saveAfter = this.$options.saveAfter;
      let saveComplete = this.$options.saveComplete;
      try {
        // 判断是否问卷提交
        if (this.page && this.page.pageType === "question") {
          // 问卷验证与数据处理
        } else {
          this.resetValidate();
        }
        //轮询执行所有组件提交前事件
        await this.$postBefore();
        //执行表单验证
        try {
          let valideResult = await this.formValidate(options);
          if (valideResult && valideResult.length > 0) {
            let field = _.find(this.postFields, (it) => {
              return it.$formName == valideResult[0].target
            })
            if (field && field.$el) {
              field.$el.scrollIntoView({
                behavior: "smooth",
                block: "start",
                inline: "nearest"
              });
            }
            return;
          }
        } catch (ex) {
          dsf.warn(ex);
          return;
        }
        //保存时强行结束掉所有子表的编辑状态
        for (let i = 0; i < this.subTables.length > 0; i++) {
          this.subTables[i].editAllEnd && this.subTables[i].editAllEnd();
        }
        if (saveBefore) {
          //执行表单保存前钩子函数
          await promiseFn.call(this, saveBefore, [options], true);
        }
        //表单保存
        let result = null;
        try {
          result = await this.$save(options);
        } catch (ex) {
          throw ex;
        }
        //更新表单数据
        saveResult = result;
        //轮询执行所有组件的postAfter
        await this.$postAfter(result);
        if (saveAfter) {
          //执行表单保存后钩子函数
          await promiseFn.call(this, saveAfter, [saveResult.data, options]);
        }
        if (!options.isSilent) {
          dsf.layer.message(options.successMsg, true);
        }
        return result;
      } catch (err) {
        //抛出错误代码，有state表示后端返回的错误，20001的话提示报错错误,其他的显示后端返回的错误代码
        if (dsf.type(err) == "object" && dsf.hasOwn(err, "state")) {
          let message = err.state === 20001 ? options.errorMsg : err.message;
          dsf.layer.message(message, false);
        }
        //如果错误直接是文本，直接提示错误
        else if (dsf.type(err) === "string") {
          dsf.layer.message(err, false);
        }
        //程序遇到的异常(error)
        else if (dsf.type(err) === "error") {
          dsf.layer.message(options.errorMsg, false);
          dsf.error(err);
        }
        saveResult = null;
        //轮询执行所有组件的postError
        await this.$postError();
      } finally {
        if (saveResult?.data) {
          this.$data._postState = 'FORMSAVE';
          this.$data._postState = '';
        }
        this.ignoreValidate = false;
        if (options.isRefresh && saveResult?.data?._id) {
          this.reloadParentViewData();
        }
        if (saveComplete) {
          //执行表单保存完成（无论成功失败）的钩子函数
          saveComplete.call(this, saveResult);
        }
        this.loading = false;
        saveResult = null;
      }
    },
    //表单保存成功后获取保存结果中的主键更新url中的id参数
    $updateUrl(result) {
      let id = result._id;

      if (this?.$frame?.independent) {
        this.$frame.urlChange({
          pageName: this.pageName,
          nameSpace: this.nameSpace,
          query: { ...this.$localParams, id: id },
        }, true)
      }
      //如果页面在dialog中不更新url中的id参数
      else if (this.$dialog) {
        // this.queryString.id = result._id;
        this.$dialog.currentView && this.$dialog.currentView.urlChange({
          pageName: this.pageName,
          nameSpace: this.nameSpace,
          query: { ...this.$localParams, id: id },
        }, true)
      } else {
        let params = dsf.mix({}, this.$route.query, {
          id: result._id
        });
        //更新url添加id参数
        this.$router.replace({
          path: this.$route.path,
          query: params
        });
      }
    },
    //提交数据
    async $save(options) {
      let _this = this;
      let data = dsf.mix(true, {}, this.$viewData);
      //处理子表数据

      _.each(this.subTables, (table) => {
        let key = table.metadata_fullcode;
        let tableData = data[key] || [];
        if (tableData.length == 1) {
          //判断当前行是否所有值都为空
          let isValid = false;
          for (let k in tableData[0]) {
            if (k.indexOf("$") != 0) {
              if (dsf.isDef(tableData[0][k]) && !dsf.isEmptyObject(tableData[0][k])) {
                isValid = true;
                break;
              }
            }
          }
          if (!isValid) {
            tableData = []
          }
        }
        if (table.deleted) {
          let deletedRows = [];
          _.each(table.deleted, (delRow) => {
            if (delRow["_id"]) {
              let row = _.find(tableData, (row) => row["_id"] == delRow["_id"]);
              if (!row) {
                delRow[key + ".ds_deleted"] = 1;
                deletedRows.push(delRow);
              }
            }
          });
          data[key] = tableData.slice(0).concat(deletedRows);
        }

        _.each(tableData, (row, i) => {
          if (row[key + ".ds_deleted"] != 1) {
            row[key + ".ds_deleted"] = 0;
          }
          row[key + ".ds_order"] = i;
        });
      });
      // this.correctPostData(data);
      let postDataBefore = this.$options.postDataBefore;
      let postDataAfter = this.$options.postDataAfter;
      //数据被提交前的处理
      if (postDataBefore) {
        await promiseFn.call(this, postDataBefore, [data, options]);
      }
      let params = {
        namespace: this.nameSpace,
        pageName: this.pageName,
        data: data,
        attach: JSON.stringify({})
      };
      if (this.$dataKey) {
        params.data["_id"] = this.$dataKey;
      }
      //保存时参数处理
      let saveParams = this.getSavePostParams;
      if (saveParams) {
        saveParams.call(this, params);
      }
      //提交是忽略的系统字段
      let systemKeys = [dsf.config.kw.updatetime, dsf.config.kw.updateusername, dsf.config.kw.updateuserid, dsf.config.kw.createuserid, dsf.config.kw.createusername, dsf.config.kw.createtime];
      params.data = JSON.stringify(params.data, function (key, val) {
        // $开头的数据为前端产生的结构，不提交服务器否则会导致服务端
        if (!key.startsWith("$")) {
          if (_this.metadataDict[key] && _this.metadataDict[key].allowPost === false) {
            return undefined;
          }
          //判断当前key是否是系统字段，如果是则忽略
          let ignore = _.find(systemKeys, (it) => key.endsWith(`.${it}`));
          return ignore ? undefined : val;
        }
        return undefined;
      });
      try {
        //调用接口
        let result = await this.saveAPI(params);
        if (result && result.data) {
          let data = result.data;
          let successCode = "20000";
          if (data.state == successCode) {
            _.each(this.subTables, (table) => {
              table.deleted = [];
            });
          }
          if (postDataAfter) {
            //提交后的数据处理
            await promiseFn.call(this, postDataAfter, [data, options]);
          }
          if (data.state == successCode) {
            return data;
          } else {
            throw data;
          }
        }
      } catch (err) {
        throw err;
      }
    },
    //调用表单保存api
    saveAPI(params) {
      return this.$webAPI.formSave(params, true);
    },
    getFormComponentByName(formName) {
      let el = document.querySelector("[form-name='" + formName + "']");
      if (el && el.__vue__) {
        return el.__vue__;
      }
      return null;
    },
    getSubTable(key) {
      return _.find(this.subTables, (it) => {
        return it.metadata_fullcode == key;
      })
    }

  },
  watch: {
  }
};

//子表数据是否是个空行（没有任何数据）
function isEmptyRow(row) {
  let mark = false;
  for (let k in row) {
    if (k.indexOf("$")) {
      k.indexOf()
    }
  }
}

//获取组件所属的子表
function getSubTableForCtrl(ctrl) {
  if (ctrl.$list && ctrl.$list.isOneToMany) {
    return ctrl.$list;
  }
  return null;
}

function promiseFn(callback, params, isNextTick) {
  return new Promise((resolve, reject) => {
    if (callback) {
      let next = (mark) => {
        if (dsf.isUnDef(mark) || mark === true) {
          if (isNextTick) {
            this.$nextTick(() => {
              resolve();
            });
          } else {
            resolve();
          }
        } else {
          reject(mark);
        }
      };
      params = params || [];
      if (callback) {
        callback && callback.apply(this, [next, ...params]);
      }
    } else {
      resolve();
    }
  });
}


//开放全局变量，二次开发时无法使用import时可以使用该全局变量混入
if (window) {
  window.$$form = form;
}
export default form;
