var isAuthenticated = false;

export default {
  type: "wechat",
  init() {
    let promist = new Promise((resolve, reject) => {
      var el = document.createElement('script');
      el.src = "https://res.wx.qq.com/open/js/jweixin-1.6.0.js"
      document.body.appendChild(el);
      el.onload = function () {
        resolve()
      }
      el.onerror = function () {
        reject();
      }
    });
    return promist.then(() => {
      //js加载完毕，调用鉴权接口
      return authentication();
    })
  },
  jsApiList: ["onMenuShareTimeline",
    "onMenuShareAppMessage",
    "onMenuShareQQ",
    "onMenuShareWeibo",
    "onMenuShareQZone",
    "startRecord",
    "stopRecord",
    "onVoiceRecordEnd",
    "playVoice",
    "pauseVoice",
    "stopVoice",
    "onVoicePlayEnd",
    "uploadVoice",
    "downloadVoice",
    "chooseImage",
    "previewImage",
    "uploadImage",
    "downloadImage",
    "translateVoice",
    "getNetworkType",
    "openLocation",
    "getLocation",
    "hideOptionMenu",
    "showOptionMenu",
    "hideMenuItems",
    "showMenuItems",
    "hideAllNonBaseMenuItem",
    "showAllNonBaseMenuItem",
    "closeWindow",
    "scanQRCode",
    "chooseWXPay",
    "openProductSpecificView",
    "addCard",
    "chooseCard",
    "openCard"
  ],
  methods: {
    authentication,
    camera: {
      //扫描二维码
      async scan(opt = {}) {
        opt = {
          needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
          scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
          ...opt
        }
        if (!isAuthenticated) await authentication().catch(() => { })
        return new Promise((resolve, reject) => {
          if (!isAuthenticated) return reject('未鉴权通过')
          wx.scanQRCode({
            needResult: opt.scanType, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: opt.scanType, // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
              resolve((res && res.resultStr) || '')
            },
            fail: function (err) {
              reject(err)
            },
            complete: function (val) { }
          })
        })
      }
    },
    // 应用就绪 api 可用
    deviceready(cb) {
      if (!dsf.type(cb) == 'function') return console.warn('deviceready 必须传入一个回调函数')
      document.addEventListener(
        "deviceready",
        () => {
          cb()
        },
        false
      );
    },
  }
}

//鉴权
function authentication() {
  let url = 'api/wechat/auth/js/token';//获取微信签名
  return new Promise((resolve, reject) => {
    if (isAuthenticated) resolve()
    dsf.http.get(url, { url: window.encodeURIComponent(window.location.href) })
      .done((res) => {
        if (res.data.jsApiTicket) {
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: res.data.appId, // 必填，公众号的唯一标识
            timestamp: res.data.timestamp, // 必填，生成签名的时间戳
            nonceStr: res.data.nonceStr, // 必填，生成签名的随机串
            signature: res.data.signature, // 必填，签名
            jsApiList: ['scanQRCode'] // 必填，需要使用的JS接口列表
          });
          wx.ready(function () {
            isAuthenticated = true;
            resolve(true)
          });
          wx.error(function (res) {
            isAuthenticated = false
            reject(res)
          });
        }
      })
      .error((res) => {
        isAuthenticated = false
        reject(res)
      })
  })
}