export default {
  type: "dingding",
  init() {
    let promist = new Promise((resolve, reject) => {
      var el = document.createElement('script');
      el.src = "https://g.alicdn.com/dingding/dingtalk-jsapi/2.10.3/dingtalk.open.js"
      document.body.appendChild(el);
      el.onload = function () {
        resolve()
      }
      el.onerror = function () {
        reject();
      }
    });
    return promist.then(() => {
      //js加载完毕，调用鉴权接口
      return this.authentication();
    })
  },
  //鉴权
  authentication() {
    return new Promise((resolve, reject) => {

    })
  },
  methods: {
    //扫描二维码
    scan() {
      return new Promise(()=>{
      })
    }
  }
}