// }

//初始化控件数据源(下拉框、单选多选等)
function initEnum() {
  for (let k in this.context.$$viewInitData) {
    let d = this.context.$$viewInitData[k];
    if (d && d.dataSource) {
      this.$set(this.context.$$enum, k, d.dataSource);
    }
  }
}


function getValidateIsDisabledFn(formName) {
  if (this.validateRules[formName]) {
    let rule = this.validateRules[formName].disabled || dsf.noop;
    return rule.bind(this);
  }
  return dsf.noop;
}

//元数据配置验证器
function metadataValidateConfigValidator(value, scope, formName, metadata) {
  if (metadata && metadata.validate && metadata.validate.config) {
    let valueAttributes = metadata.valueAttributes || [];
    let validateConfig = metadata.validate.config;
    for (let i = 0; i < valueAttributes.length; i++) {
      let va = valueAttributes[i];
      let valiConfig = validateConfig[va];
      if (valiConfig) {
        let v = null;
        if (valueAttributes.length == 1) {
          v = value
        } else {
          //有可能会是数组，比如下拉框多选，多选组件
          if (dsf.type(value) == "array") {
            let arr = value
            v = _.map(arr, va).join(",");
          } else {
            v = value ? value[va] : "";
          }
        }
        // let v = valueAttributes.length == 1 ? ctrl.value : (ctrl.value ? ctrl.value[va.code] : '');
        //验证是否必填
        if (valiConfig.required) {
          if (dsf.validate("isNull", v) === true) {
            return new ValidateError(dsf.express.replace(valiConfig.errorMsg.required, valiConfig), formName, {
              attributes: va
            });
          }
        }
        if (v != "" && dsf.isDef(v)) {
          if (valiConfig.isNumber) {
            //验证是否是一个数字
            if (dsf.validate("isNumber", v) === false) {
              return new ValidateError(dsf.express.replace(valiConfig.errorMsg.isNumber, valiConfig), formName, {
                attributes: va
              });
            }
            //验证数字最大值和最小值
            if (dsf.isDef(valiConfig.numberMax) && valiConfig.numberMax != "") {
              if (dsf.validate("isNumberOver", v, valiConfig.numberMax) === true) {
                return new ValidateError(dsf.express.replace(valiConfig.errorMsg.numberMax, valiConfig), formName, {
                  attributes: va
                });
              }
            }
            if (dsf.isDef(valiConfig.numberMin) && valiConfig.numberMin != "") {
              if (dsf.validate("isNumberUnder", v, valiConfig.numberMin) === true) {
                return new ValidateError(dsf.express.replace(valiConfig.errorMsg.numberMin, valiConfig), formName, {
                  attributes: va
                });
              }
            }
            //验证数字精度
            if (valiConfig.precision) {
              if (dsf.validate("precision", v, valiConfig.precision) === false) {
                return new ValidateError(dsf.express.replace(valiConfig.errorMsg.precision, valiConfig), formName, {
                  attributes: va
                });
              }
            }
          } else {
            //验证字符串是否超出长度
            if (valiConfig.max) {
              if (dsf.validate("maxLength", v, valiConfig.max) === true) {
                return new ValidateError(dsf.express.replace(valiConfig.errorMsg.max, valiConfig), formName, {
                  attributes: va
                });
              }
            }
            if (valiConfig.min) {
              if (dsf.validate("minLength", v, valiConfig.min) === true) {
                return new ValidateError(dsf.express.replace(valiConfig.errorMsg.min, valiConfig), formName, {
                  attributes: va
                });
              }
            }
          }
          //验证格式
          if (valiConfig.express) {
            if (dsf.validate(valiConfig.express, v) == false) {
              return new ValidateError(dsf.express.replace(valiConfig.errorMsg.express, valiConfig), formName, {
                attributes: va
              });
            }
          }
        }
      }
    }
  }
}

var div = document.createElement("DIV");
//处理富文本编辑器的值，因为富文本会产生html标记对，表单验证时获取实际的文本
function getEditorConent(value) {
  div.innerHTML = value;
  //是否存在贴图
  let isImg = div.querySelector("img");
  if (isImg) {
    return div.innerHTML;
  }
  else {
    return div.innerText.trim();
  }
}

//获取组件的验证方法
function getValidateFunction(key, dict, formName) {
  let fn = null;
  let meta = dict[key];
  //优先读取二次开发附加的validateRule
  let rule = this.validateRules[key];
  if (rule && (dsf.isFunction(rule) || rule.handler)) {
    fn = ruleProxy.call(this, (rule.handler || rule), formName, meta);
    if (fn) {
      fn = fn.bind(this);
    }
    else {
      fn = null;
    }
  }
  //获取元数据验证格式
  else if (meta && meta.validate) {
    if (!meta.isUser) {
      fn = metadataValidateConfigValidator.bind(this);
    }
  }

  //优先读取二次开发附加的validateRule
  // if (field.validateRule) {
  //   fn = ruleProxy.call(this, this.validateRules[field.validateRule]).bind(this, value, field);
  // }
  // //读取组件是否自带内置验证函数
  // else if (field.builtInValidate) {
  //   fn = ruleProxy.call(this, field.builtInValidate).bind(field, value, field);
  // }
  // //获取元数据验证格式
  // else if (field.metadata && field.metadata.validate) {
  //   fn = metadataValidateConfigValidator.bind(field, value, field);
  // }
  return fn || dsf.noop;
}

//代理执行validateRules中的规则
//返回方法必须第一个参数为当前值，当前作用域值，表单的formName，元数据
function ruleProxy(fn, formName, metadata) {
  let ruleFn = fn;
  if (ruleFn) {
    return function (value, scope) {
      let result = ruleFn.call(this, value, scope);
      if (dsf.isUnDef(result)) {
        return;
      }
      //如果函数返回一个Promise对象
      if (result.then && result.catch) {
        return Promise((resovle, reject) => {
          result.then(() => {
            resovle();
          });
          result.catch((valiResult) => {
            let message = dsf.type(valiResult) == 'string' ? valiResult : valiResult.message
            resovle(new ValidateError(message, formName, valiResult));
          });
        });
      } else {
        if (result) {
          let message = dsf.type(result) == 'string' ? result : result.message
          return new ValidateError(message, formName, result);
        }
      }
    };
  }
  return dsf.noop;
}

//错误类
function ValidateError(message, target, other) {
  this.message = message;
  this.target = target;
  if (other) {
    this.info = {
      ...other
    };
  } else {
    this.info = {};
  }
}

export default {
  inheritAttrs: false,
  inject: {
    $app: {
      default: null
    },
    $parentView: {
      default: null
    },
    $frame: {
      default: null
    },
    $dialog: {
      default: null
    },
    $isMobile: {
      default: false
    }
  },
  provide() {
    return {
      $vm: this,
      $parentView: this,
      $choiceParams: this.choiceParams
    };
  },
  props: {
    nameSpace: {
      type: String,
      default: ""
    },
    pageName: {
      type: String,
      default: ""
    },
    queryString: {
      type: Object,
      default() {
        return {};
      }
    },
    choiceParams: {
      type: Object,
      default() {
        return null;
      }
    }
  },
  data() {
    return {
      context: {
        $$viewInitData: {},
        $$viewData: {},
        $$enum: {},
        $$notAllowPostKey: [],
        $$postNullKeys: [],
        $$saved: false
      },
      staticData: {},
      validateRules: {},
      _isForm: false,
      frames: {},
      _isLoaded: false,
      _postState: "",
      isLayoutView: true,
      metadataDict: {},
      metadataKeyList: [],
      loading: false,
      flows: {},
      $forceReadOnly: false,
      $formWatchs: [],
      $choice: null,
      $isDestroyed: false,
      _isReady: false,
      _dataKey: this.queryString.id,
      _fromDesigner: true,
      _errors: [],
      workFlow: {
        ctrls: {},
        btns: {}
      }
    };
  },
  computed: {
    $dataKey: {
      get() {
        return this.$data._dataKey;
      },
      set(v) {
        this.$data._dataKey = v;
      }
    },
    $isPrototype() {
      return this.$data.$$mode == dsf.enums.viewMode.PROTOTYPE;
    },
    $staticValue() {
      return this.$data.$$staticValue;
    },
    $localParams() {
      return this.$frame.localParams;
    },
    $viewData() {
      return this.context.$$viewData;
    },
    $viewInitData() {
      return this.context.$$viewInitData;
    },
    $enum() {
      return this.context.$$enum;
    },
    $isLoaded() {
      return this.$data._isLoaded;
    },
    $isReady() {
      return this.$data._isReady;
    },
    //页面是否被keepAlive,返回page组件的isKeepAlive属性
    $keepAlive() {
      return this.$data.$$keepAlive || false;
    },
    $pageType() {
      return this.$data.$$pageType || "";
    },
    $isForm() {
      return this.$data._isForm;
    },
    $servicePrefix() {
      let servicePrefix = this?.$baseUrl || "";
      return servicePrefix;
    },
    $errors() {
      return this.$data._errors;
    }
  },

  // 页面复用时触发数据更新
  // beforeRouteUpdate(to, from, next) {
  //   // 如果页面类型为form、question、exam
  //   if (this.$data._postState == 'FORMSAVE') {
  //     next();
  //     return;
  //   }
  //   // 其他情况的页面复用
  //   this.reset();
  //   this.reloadData();
  //   next();
  // },
  beforeCreate() {
    let _this = this;
    this.$dialogArgs = null;
    this.$openerVm = null;
    Object.defineProperty(this, '$page', {
      get() {
        return this.$refs.page;
      },
      // configurable: false,
      // writable: false,
      enumerable: false
    });

    Object.defineProperty(this, '$topView', {
      get() {
        let dom = $(_this.$el)
        let pages = dom.parents('.ds-control.ds-page');
        let top = pages.get(pages.length - 1);
        if (top) {
          let page = top.__vue__;
          return page?.$vm || {};
        }
        return this
      },
      // configurable: false,
      // writable: false,
      enumerable: false
    })
  },
  created() {
    if (this.i18nMessage) {
      for (let k in this.i18nMessage) {
        let message = this.i18nMessage[k];
        this.$i18n.mergeLocaleMessage(k, {
          ["$" + this.$options.name]: message
        })
      }
    }
    if (this.$frame) {
      this.$frame.content = this;
    }
  },
  updated() { },
  activated() { },
  deactivated() { },
  mounted() {
    this.$init();
  },
  beforeDestroy() {
    this.$data.$isDestroyed = true;
    this.context.$$viewData = null;
    this.context.$$viewInitData = null;
    this.context.$$notAllowPostKey = null;
    this.$data.$choice = null;
    for (let k in this.$frames) {
      this.$delete(this.frames, k);
    }
    if (this.$frame) {
      delete this.$frame.content;
    }
    _.eachRight(this.$data.$formWatchs, (uw, i) => {
      uw();
      this.$data.$formWatchs.splice(i, 1);
    });
    this.$dialogArgs = null;
    this.$openerVm = null;
  },
  methods: {
    $addCustom(caption, props) {
      dsf.custom.add(this, caption, props);
    },
    $removeCustom(caption) {
      dsf.custom.remove(this, caption);
    },
    $clearCustom(caption) {
      dsf.custom.clear(this, caption);
    },
    getControl(ref, hash) {
      return this.$refs[ref];
    },
    //初始化页面
    async $init() {
      // let loading = dsf.layer.pc.loading();
      let viewData = null;
      new Promise((resolve) => {
        resolve();
      }).then(() => {
        if (!this.$data.$isDestroyed) {
          //获取页面初始化数据
          return this.$initData();
        }
      }).then((response) => {
        if (!this.$data.$isDestroyed) {
          //执行初始化数据完成的钩子函数
          let inited = this.$options.inited || noopHooks;
          return promiseFn(inited).call(this, response ? response.data : {});
        }
      }).then((data) => {
        if (!this.$data.$isDestroyed) {
          let _this = this;
          let _custom = {};
          try {
            _custom = data._custom ? JSON.parse(data._custom) : '{}';
          }
          catch (ex) {
            dsf.error("custom信息转换错误")
          }
          if (dsf.type(_custom) != 'object') {
            _custom = {};
          }
          data._custom = _custom;
          _this.context.$$viewInitData = data;
          initEnum.call(this);
          //初始化流程信息
          initWorkFlow.call(this);
          //初始化数据
          viewData = this.$initControls();
          //将页面显示出来
          this.$data._isLoaded = true;
          //原型设计界面加载静态数据
          if (this.$isPrototype) {
            return this.$loadFormStaticData();
          }
          //调用表单(问卷)加载数据
          else if (this.$loadData) {
            //如果存在主键参数则加载表单数据
            this.loading = true;
            return this.$loadData(this.nameSpace);
          }
        }
      }).then((response) => {
        if (!this.$data.$isDestroyed) {
          //执行查询表单数据完成的钩子
          let dataLoaded = this.$options.dataLoaded || noopHooks;
          return promiseFn(dataLoaded).call(this, response ? response.data : {});
        }
      }).then((data) => {
        if (!this.$data.$isDestroyed) {
          let _this = this;
          // dsf.log("开始为页面进行赋值了");
          if (data) {
            dsf.mix(viewData, data);
          }
          _this.context.$$viewData = viewData;
        }
      }).catch((err) => {
        dsf.httpError(err);
      }).finally(() => {
        this.loading = false;
        this.$nextTick(() => {
          //重置页面和下级组件的宽高
          let ready = this.$options.ready;
          ready && ready.call(this);
          this.$data._isReady = true;
          // 注册二次开发的监听器
          // 监听的表单值
          let formWatch = this.$options.formWatch || {};
          for (let key in formWatch) {
            let item = formWatch[key];
            let fn,
              options = {};
            if (dsf.type(item) == "function") {
              fn = item.bind(this);
            } else if (dsf.type(item) == "object") {
              if (!item.handler || dsf.type(item.handler) !== "function") {
                break;
              }
              fn = item.handler.bind(this);
              if (item.deep) {
                options.deep = true;
              }
              if (item.immediate) {
                options.immediate = true;
              }
            } else {
              break;
            }
            let unwatch = createWatcher(this, key, fn, options);
            this.$data.$formWatchs.push(unwatch);
          }
        });
      });
    },
    //调用初始化接口
    async $initData() {
      try {
        let params = {
          pageName: this.pageName,
          namespace: this.nameSpace,
          ...this.queryString,
        }
        if (this.nameSpace && this.pageName) {
          if (this.$dataKey) {
            params.id = this.$dataKey;
          }
          let result = await this.$webAPI.getInitData(params, true);
          return result.data;
        }
        else {
          await dsf.noop();
          return {};
        }
      }
      catch (err) {
        throw err;
      }
    },
    $getDefaultValue(key) {
      if (dsf.isDef(this.$viewInitData[key]) && dsf.isDef(this.$viewInitData[key].defaultValue)) {
        let v = this.$viewInitData[key].defaultValue;
        let type = dsf.type(v);
        if (type == "object" || type == "array") {
          v = dsf.mix(true, type == "object" ? {} : [], v);
        }
        return v;
      }
    },
    $dataFormat(k, params, v) {
      if (params) {
        let filter = Vue.filter(k);
        if (filter) {
          return filter(v, params);
        }
      }
      return v;
    },
    $clone(obj) {
      return JSON.parse(JSON.stringify(obj));
    },
    $closeWindow() {
      let dialog = this.$dialog;
      if (dialog) {
        dialog.close();
      } else {
        if (window.opener) {
          if (window.opener.openerVms && window.opener.openerVms[window.name]) {
            delete window.opener.openerVms[window.name];
          }
          window.opener.openerVm = null;
          window.dialogArgs = null;
          window.opener = null;
          window.close();
        }
        else {
          if (window.frameElement && window.frameElement.$dialog) {
            window.frameElement.$dialog.close();
          }
        }
      }
    },
    //获取
    $setControlPropsByWorkFlow(ctrlProps, type) {
      let flowdebugger = this.queryString._wfdebug == 1;
      let initData = this.$viewInitData;
      if (!initData || !initData.workflow) {
        return;
      }
      if (type == "formControl") {
        let flowProps = ["readOnly", "visible"];
        if (this.workFlow.ctrls) {
          let key = ctrlProps.isFormControl ? ctrlProps.metadata_fullcode : "";
          let workFlowCtrl = this.workFlow.ctrls[key];
          if (workFlowCtrl) {
            _.each(flowProps, (prop) => {
              if (dsf.isDef(workFlowCtrl[prop])) {
                if (flowdebugger) {
                  dsf.log(`"组件：${ctrlProps.caption + (ctrlProps.label ? "(" + ctrlProps.label + ")" : "")}的${prop}属性被流程设置为${workFlowCtrl[prop]}`);
                }
                ctrlProps[prop] = workFlowCtrl[prop];
              }
            });
          }
        }
      } else if (type == "buttons") {
        if (initData.workflow && initData.workflow.btns) {
          _.each(ctrlProps.buttons, (btn) => {
            let workFlowBtn = this.workFlow.btns[btn.id];
            if (workFlowBtn && dsf.isDef(workFlowBtn.visible)) {
              if (flowdebugger) {
                dsf.log(`按钮：${btn.caption}的visible属性被流程设置为${workFlowBtn.visible}`);
              }
              btn.visible = workFlowBtn.visible;
            }
          });
        }
      } else if (type == "block") {
        let flowProps = ["readOnly", "visible"];
        let key = ctrlProps.caption || "";
        let workFlowCtrl = this.workFlow.ctrls[key];
        if (workFlowCtrl) {
          _.each(flowProps, (prop) => {
            if (dsf.isDef(workFlowCtrl[prop])) {
              if (flowdebugger) {
                dsf.log(`"组件：${ctrlProps.caption}的${prop}属性被流程设置为${workFlowCtrl[prop]}`);
              }
              ctrlProps[prop] = workFlowCtrl[prop];
            }
          });
        }
      }
    },
    $initControls() {
      let vd = {};
      let controls = this.$controls;
      let initData = this.$viewInitData;
      for (let k in controls) {
        let ctrl = controls[k];
        if (ctrl.metadata) {
          let key = ctrl.metadata_fullcode;
          if (ctrl.metadata.level == 0) {
            if (initData && initData[key]) {
              //将默认值赋予viewData
              if (dsf.isDef(this.$getDefaultValue(key))) {
                vd[ctrl.metadata_fullcode] = this.$getDefaultValue(key);
              }
            }
          } else if (ctrl.metadata.level == 1 && ctrl.isOneToMany) {
            if (dsf.isDef(this.$getDefaultValue(key))) {
              vd[ctrl.metadata_fullcode] = this.$getDefaultValue(key);
            }
          }
          //如果isview=1页面只读
          if (ctrl.isFormControl && this.$data.$forceReadOnly) {
            ctrl.readOnly = true;
            continue;
          }
          // else {
          //   ctrl.readOnly = false;
          // }
          this.$setControlPropsByWorkFlow(ctrl, "formControl");
        } else if (ctrl.ctrlType == "dsf.buttonbar") {
          this.$setControlPropsByWorkFlow(ctrl, "buttons");
        } else if (ctrl.ctrlType == "dsf.viewpart" || ctrl.ctrlType == "dsf.section") {
          this.$setControlPropsByWorkFlow(ctrl, "block");
        }
        if (this.$staticValue[ctrl.caption]) {
          ctrl.staticValue = this.$staticValue[ctrl.caption];
        }
        let cust = this.$viewInitData._custom || {};
        dsf.mix(ctrl, cust[ctrl.caption] || {});
      }
      return vd;
    },
    reset() {
      for (let k in this.$refs) {
        if (this.$refs[k] && this.$refs[k].reset) {
          this.$refs[k].reset();
        }
      }
    },
    reloadData() {
      for (let k in this.$refs) {
        if (this.$refs[k] && this.$refs[k]._isVue && this.$refs[k].reloadData) {
          this.$refs[k].reloadData();
        }
      }
    },
    //获取表单静态数据
    $loadFormStaticData() {
      return new Promise((resolve, reject) => {
        this.$nextTick(() => {
          let data = {};
          for (let k in this.$refs) {
            let ctrl = this.$refs[k];
            if (!ctrl.designId) {
              continue;
            }
            let staticValue = this.$staticValue[ctrl.caption];
            if (staticValue && ctrl.isFormControl && !ctrl.$dataHash) {
              staticValue = ctrl.$getRealStaticValue(staticValue);
              data[ctrl.metadata_fullcode] = staticValue;
            }
          }
          resolve({ data });
        })
      })

    },
    reloadParentViewData() {
      // if (window.$isMobile) {
      //   //移动端将当前页面上一个页面的路由记录到vuex中,下次加载的时候刷新从队列中判断进行页面更新
      //   if (window.$isMobile && window.$$state) {
      //     let last = window.$$state.routeChain[window.$$state.routeChain.length - 2];
      //     if (last && last.path && last.path != "/") {
      //       this.$store && this.$store.commit("refreshQueue/add", last.path);
      //     }
      //   }
      // } else {
      //   //pc端获取当前页面的父页面并刷新父页面数据
      //   if (this.$openerVm) {
      //     //更新父页面数据
      //     this.$openerVm && this.$openerVm.reloadData && this.$openerVm.reloadData();
      //   }
      // }
      //刷新父页面函数，由pcLayoutView.js和mobileLayoutView.js重写
    },
    //返回$choice中组件的选中对象
    yes() {
      let result = this.$data.$choice.getReturnValue();
      return result;
    },
    //添加表单组件
    $addFiled(ctrl) {
      this.$nextTick(() => {
        this.updateMetaDataDict(ctrl);
      });
    },
    //表单组件被销毁
    $removeFiled(ctrl) {
      if (this.metadataDict[ctrl.formName]) {
        //refs用来存储组件实例，组件移除时将refs中的实例也移除掉
        dsf.array.remove(this.metadataDict[ctrl.formName].refs || [], ctrl)
        // console.log(ctrl.formName,this.metadataDict[ctrl.formName].refs)
      }
    },
    // 更新metadataDict，因为页面中有存在动态创建的组件（子表，动态布局）
    updateMetaDataDict(ctrl) {
      if (this.metadataDict) {
        let formName = ctrl.formName;
        let fullCode = ctrl.metadata_fullcode;
        let metadata = ctrl.metadata;
        if (!metadata || !formName) {
          return;
        }
        let arr = formName.split(".");
        let prefix = arr[0];
        let name = arr[1] ? arr[1] : "";
        let isUser = false;
        if (!this.metadataDict[formName]) {
          this.metadataDict[formName] = {
            type: "field",
            fieldsLength: metadata.valueAttributes && metadata.valueAttributes.length,
            level: 0,
            valueAttributes: _.map(metadata.valueAttributes, "code"),
            refs: []
          };
          isUser = true;
        }
        this.metadataDict[formName].refs = this.metadataDict[formName].refs || [];
        this.metadataDict[formName].valueIsHtml = ctrl.valueIsHtml === true ? true : false
        this.metadataDict[formName].validate = metadata.validate;
        this.metadataDict[formName].refs.push(ctrl);
        dsf.array.ensure(this.metadataKeyList, formName);
        if (this.metadataDict[formName]) {
          this.metadataDict[formName].isUser = isUser;
          if (ctrl.validateRule) {
            this.metadataDict[formName].validateRule = ctrl.validateRule;
          }
        }
      }
    },

    //获取所在作用域的元数据字典
    getScopeMetaDataDict(component) {
      if (dsf.isEmptyObject(this.metadataDict)) {
        return {};
      }
      let metadata = component.metadata;
      if (!metadata || !component.metadata_fullcode) {
        return {};
      }
      let fullCode = component.metadata_fullcode;
      if (this.metadataDict[fullCode]) {
        if (this.metadataDict[fullCode].inSubTable) {
          let result = {};
          _.each(this.metadataDict, (el, i) => {
            if (el.inSubTable === this.metadataDict[fullCode].inSubTable) {
              result[i] = el;
            }
          });
          return result;
        } else {
          let result = {};
          _.each(this.metadataDict, (el, i) => {
            if (dsf.isUnDef(el.inSubTable)) {
              result[i] = el;
            }
          });
          return result;
        }
      }
    },
    //添加禁止提交的数据key
    addNotAllowPostKey(key) {
      dsf.array.ensure(this.context.$$notAllowPostKeys || [], key);
    },
    //移除禁止提交的数据key
    removeNotAllowPostKey(key) {
      dsf.array.remove(this.context.$$notAllowPostKeys || [], key);
    },
    //添加提交给后端null值得key
    addPostNullKey(key) {
      dsf.array.ensure(this.context.$$postNullKeys || [], key);
    },
    //移除提交给后端null值得key
    removePostNullKey(key) {
      dsf.array.remove(this.context.$$postNullKeys || [], key);
    },
    resize() {
      this.$page && dsf.resizeComponent(this.$page);
    },
    //重置表单验证结果
    resetValidate() {
      var fields = this.postFields;
      this.clearErrors();
      _.each(fields, (field) => {
        field.errors = [];
      });
    },
    //将错误推送到队列中
    pushError(errors) {
      let _errors = dsf.type(errors) == 'array' ? errors : [errors];
      _errors = _.filter(_errors, (it) => dsf.isDef(it));
      let current_errors = this.$data._errors;
      let keys = _.map(_errors, "target");
      dsf.array.removeWith(current_errors, (it) => {
        return keys.indexOf(it.target) >= 0;
      })
      this.$data._errors.push(..._errors);
    },
    //清空所有错误
    clearErrors() {
      this.$data._errors = [];
    },
    //清理指定组件的错误信息
    removeErrorsByTarget(target) {
      dsf.array.removeWith(this.$data._errors, (it) => it.target == target);
    },
    //清理指定列(子)表行的错误
    removeErrorsByRowHash(hash) {
      dsf.array.removeWith(this.$data._errors, (it) => it.target.endsWith(hash));
    },
    //获取自定义校验规则是否被禁用的方法
    $getValidateIsDisabledFn(formName) {
      return getValidateIsDisabledFn.call(this, formName)
    },
    //执行元数据校验规则
    $metadataValidateConfigValidator(value, scope, formName, metadata) {
      return metadataValidateConfigValidator.call(this, value, scope, formName, metadata)
    },
    //获取html类组件内容（排除html标签）
    $getEditorConent(value) {
      return getEditorConent(value);
    },
    //获取校验规则
    $getValidateFunction(key, dict, formName) {
      return getValidateFunction.call(this, key, dict, formName)
    },
    //执行校验
    async $execValidateFns(fns) {
      let errors = [];
      return new Promise((resolve, reject) => {
        Promise.all(fns).then((result) => {
          _.each(result, (it) => {
            if (it && it instanceof $$ValidateError) {
              let error = {
                'target': it.target,
                'info': it.info,
                'message': it.message
              }
              errors.push(error);
            }
          });
          !errors.length ? resolve() : reject(errors);
        })
          .catch((err) => {
            dsf.error(err)
          })
      })
    }
  },
  watch: {
    //如果参数id变化更新$dataKey
    "queryString.id": function (v) {
      if (this.$isForm) {
        this.$dataKey = v;
      }
    },
    $isLoaded: {
      handler(nv, ov) {
        if (nv != ov && nv === true) {
          this.$nextTick(() => {
            //调用布局resize
            this.resize();
            this.$emit("loaded", this);
          });
        }
      }
    }
  }
};


//开放全局变量，二次开发时无法使用import时可以使用该全局变量混入
if (window) {
  window.$$ValidateError = ValidateError;
}