import $wechat from './wechat';
import $dingding from './dingding'
import $native from './native';

const driveList = {
  $wechat,
  $dingding,
  $native
}


export default {
  $init(allow) {
    let driver = null;
    allow = allow || ['wechat', 'dingding', 'native'];
    for (let i = 0; i < allow.length; i++) {
      let it = allow[i];
      if (dsf.client[it] && dsf.client[it]()) {
        driver = driveList['$' + it];
        break;
      }
    }
    if (driver) {
      this.$type = driver.type;
      return driver.init()
        .then(() => {
          dsf.mix(this, driver.methods);
          this.$inited = true;
        });
    }
    else {
      //如果为匹配到客户端则表示是普通浏览器打开
      return new Promise((resolve, reject) => {
        this.$type = null;
        this.$inited = true;
        resolve();
      });
    }
  },
  $type: null,
  $inited: false
}