import control from "./control";

const VALUE_CHANGE = "model-value-mutation";
const VALUE_UPDATE_SYNC = "update:value";
//表单组件在列表中触发变化
const triggerDataGridRowChange = _.debounce(function (ctrl) {
  ctrl.$list.rowDataChange(ctrl.local)
}, 100)


export default {
  model: {
    prop: "value",
    event: VALUE_CHANGE
  },
  props: {
    //是否是表单组件
    isFormControl: {
      type: Boolean,
      default: true
    },
    //表单项的名称
    label: {
      type: String,
      default: ""
    },
    //是否显示label
    showLabel: {
      type: Boolean,
      default: true
    },
    //是否只读
    readOnly: {
      type: Boolean,
      default: false
    },
    //是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    //保存时是否提交到后端
    allowPost: {
      type: Boolean,
      default: true
    },
    //是否提交一个null值给服务器
    isPostNull: {
      type: Boolean,
      default: true
    },
    //是否有必填标识
    required: {
      type: Boolean,
      default: false
    },
    //值，根据组件内容不同可能有不同类型，文本框为字符串，下拉框为{text:'',value:''}对象
    value: {
      type: [String, Number, Object, Array, Boolean, Date],
      default() {
        return null;
      }
    },
    //提示文本（移动端）
    dataCapion: {
      type: String,
      default: ""
    },
    //提示语音mp3地址（移动端）
    dataAudioUrl: {
      type: String,
      default: ""
    },
    //提示视频mp4地址（移动端）
    dataVideoUrl: {
      type: String,
      default: ""
    },
    //元数据
    metadata: {
      type: Object,
      default() {
        return null;
      }
    },
    //只读状态显示内容
    readOnlyShowText: {
      type: String,
      default: "text"
    },
    //元数据代码
    metadata_fullcode: {
      type: String,
      default: ""
    },
    //是否简易模式
    simple: {
      type: Boolean,
      default: false
    },
    //元数据等级，主表为0子表或者子表组件为1
    metaDataLevel: {
      type: Number,
      default: 0
    },
    //组件在列表或者子表中简易状态下数据格式化
    formatter: {
      type: String,
      default: ""
    },

    //validateRule、isValidate已过时后续不要再使用==================
    //验证规则名称
    validateRule: {
      type: String,
      default: ""
    },
    //是否需要验证
    isValidate: {
      type: Boolean,
      default: true
    },
    //validateRule、isValidate已过时后续不要再使用==================

    //所在栅格布局占比
    colSpan: {
      type: Number,
      default: 24
    },
    labelWidth: {
      type: Number,
      default: 120
    },
    //产生文本代理,生成模板时不创建组件标记对，使用<span>标签代替
    textProxy: {
      type: Boolean,
      default: false
    },
    //文本代理模式下前缀图标
    textProxyIcon: {
      type: String,
      default: ""
    },
    //文本代理模式下前缀图标颜色
    textProxyIconColor: {
      type: String,
      default: ""
    },
    // 是否即时触发审核
    immediatelyAudit: {
      type: Boolean,
      default: false
    },
    //组件在表单中的name
    // formName: {
    //   type: String,
    //   default: ''
    // }
  },
  data() {
    return {
      gridRowChangeKey: "change",
      _errors: []
    };
  },
  mixins: [control],
  computed: {
    validating() {
      return this?.$form?.validating
    },
    errors: {
      get() {
        if (this?.$vm?.$errors) {
          let errors = _.filter(this?.$vm?.$errors, (it) => it.target == this.$formName)
          return errors
        }
        else {
          return this.$data._errors;
        }

      },
      set(value) {
        if (this?.$form?.pushError) {
          this.$form.pushError(value);
        }
        else {
          return this.$data._errors.push(...value);
        }
      }
    },
    getCss() {
      return [
        ...this.$getClass(),
        this.errors.length > 0 ? "ds-error" : "",
        this.showLabel ? "ds-has-label" : "",
        this.required ? "ds-required" : "",
        this.readOnly ? "ds-readonly" : "",
        !this.showLabel && this.required ? "ds-nolabel-required" : '',
        this.disabled ? "ds-disabled" : "",
      ];
    },
    errorsMsg() {
      return _.map(this.errors, "message").join(",");
    },
    $labelWidth() {
      if (this.$page) {
        return this.$page.formItemLabelWidth || this.labelWidth || 120;
      }
      return this.labelWidth;
    },
    formName() {
      return this.$attrs['form-name'] || this.metadata_fullcode || ""
    },
    $formName() {
      let formName = this.formName;
      if (formName) {
        return this.formName + (this.$dataHash ? "-" + this.$dataHash : '');
      }
      return "";
    }
  },
  created() {
    if (!this.isDesign && this.$vm) {
      this.$vm.$addFiled(this);
    }
    if (!this.isDesign && this.$dynamicForm) {
      this.$dynamicForm.$addFiled(this);
    }
  },
  mounted() {
    if (this?.$el?.nodeType == 1) {
      this.$el.setAttribute("form-name", this.$formName);
    }
  },
  updated() {
    if (this?.$el?.nodeType == 1) {
      this.$el.setAttribute("form-name", this.$formName);
    }
  },
  beforeDestroy() {
    if (!this.isDesign && this.$vm) {
      this.$vm.$removeFiled(this);
    }
    if (!this.isDesign && this.$dynamicForm) {
      this.$dynamicForm.$removeFiled(this);
    }
  },
  methods: {
    showHelper() {
      this.$openDialog({
        title: "详细描述",
        template: `<div v-html="params.value"></div>`,
        width: "30vw",
        height: "50vh",
        params: {
          value: this.dataCapion
        }
      });
    },
    getLabelWidth(isToRem) {
      return isToRem ? this.pxToRem(this.$labelWidth + "px") : this.$labelWidth + "px";
    },
    getLabelWidthStyle(isToRem) {
      if (!isToRem) {
        if (this.$labelWidth && this.showLabel) {
          return {
            width: this.$labelWidth + "px"
          };
        }
        return {};
      } else {
        if (this.$labelWidth && this.showLabel) {
          return {
            width: this.pxToRem(this.$labelWidth + "px")
          };
        }
        return {};
      }
    },
    getFormItemBlockMarginLeft() {
      if (this.$labelWidth && this.showLabel) {
        return {
          "margin-left": this.$labelWidth + "px"
        };
      }
      return {};
    },
    getShowContent() {
      if (dsf.type(this.value) == "object") {
        return this.value[this.readOnlyShowText];
      } else if (dsf.type(this.value) == "array") {
        return _.map(this.value, this.readOnlyShowText).join(",");
      } else {
        return this.value;
      }
    },
    $emitValueChange(v) {
      //让组件既支持v-model绑定值又支持:value.sync绑定
      let listeners = this.$listeners || {};
      //表示v-model绑定
      if (listeners[VALUE_CHANGE]) {
        this.$emit(VALUE_CHANGE, v);
      }
      //表示value.sync绑定
      else if (listeners[VALUE_UPDATE_SYNC]) {
        this.$emit(VALUE_UPDATE_SYNC, v);
      }
    },
    async emitFormValidate(v, i) {
      //判断如果是表单或者在列表中
      if (this.$form) {
        let localIndex = null;
        if (this.$list) {
          localIndex = this.local.$index;
        }
        return await this.$vm.validateField(this.value, (dsf.isDef(i) ? i : localIndex), this);
      }
      return [];
    },
    emitValueChange(v) {
      this.$emitValueChange(v);
      this.$dispatch("value-change", v);
    },
    $dispatch(name, args) {
      control.methods.$dispatch.call(this, name, args);
      //如果数据在列表
      if (!this.isDesign && !this.$isPrototype) {
        if (name == this.gridRowChangeKey && this.$list && this.local?.$hash) {
          if (this.metaDataLevel != 1 && this.$list.isRowDataChangePost) {
            this.$nextTick(() => {
              this.$vm.removeErrorsByRowHash(this.local?.$hash);
              this.emitGridRowChange();
            });
          }
        }
      }
    },
    emitGridRowChange() {
      triggerDataGridRowChange(this);
    }
  },
  watch: {
    allowPost: {
      handler(v, ov) {
        if (!this.isDesign && this.designId) {
          let code = this.metadata_fullcode;
          if (code) {
            if (this.$list && this.$list.isOneToMany && this.local?.$hash) {
              code = [this.local?.$hash, code].join("^");
            }
          }
          if (v) {
            this?.$vm?.removeNotAllowPostKey && this.$vm.removeNotAllowPostKey(code);
          } else {
            this?.$vm?.addNotAllowPostKey && this.$vm.addNotAllowPostKey(code);
          }
        }
      },
      immediate: true
    },
    isPostNull: {
      handler(v, ov) {
        if (!this.isDesign && this.designId) {
          let code = this.metadata_fullcode;
          if (code) {
            if (this.$list && this.$list.isOneToMany && this.local?.$hash) {
              code = [this.local?.$hash, code].join("^");
            }
          }
          if (v) {
            this?.$vm?.addPostNullKey && this.$vm.addPostNullKey(code);
          } else {
            this?.$vm?.removePostNullKey && this.$vm.removePostNullKey(code);
          }
        }
      },
      immediate: true
    }
  },
  design: {
    infoButton: true,
    settingButton: true,
    settingMenus: [
      {
        text: "校验规则",
        command: "validate-rules",
        handler() {
          dsf.designer.triggerDialogPropty('metadata.validate')
        },
        hidden() {
          return this.isOneToMany;
        }
      },
      {
        text: "默认值",
        command: "default-value",
        handler() {
          dsf.designer.triggerDialogPropty('metadata.defaultValue')
        },
        hidden() {
          return this.isOneToMany;
        }
      },
      {
        text: "静态数据维护",
        command: "static-editor",
        handler: staticEditor,
        hidden() {
          return !this.$isPrototype;
        }
      },
    ],
    settingButtonHandler: null,
    layout: {
      exclude: ["value"]
    },
    metadata: {
      include: null,
      exclude: [
        'ctrlType',
        'ctrlName',
        'value',
        'items',
        'readOnly',
        'metadata',
        'metadata_fullcode',
        'textProxy',
        'disabled',
        'validateRule',
        'labelWidth',
        'metaDataLevel',
        'visible',
        'noPadding',
        'slots',
        'tbNoPadding',
        'lrNoPadding',
        'local',
        'development',
        'designProxy',
        'designOnlySlot',
        'designId',
        'isDesign',
        'classList',
        'showLabel',
        'visible',
        'staticData'
      ],
      create(root) {
        if (!this.metadata) {
          this.metadata = {};
        }
        this.metadata.hidden = !this.visible;
        let designMetaDataNode = this?.$options?.design?.metadata;
        if (designMetaDataNode) {
          let attrs = {};
          let props = Object.keys(this.$props);
          if (designMetaDataNode.include && designMetaDataNode.include.length > 0) {
            props = designMetaDataNode.include;
          }
          _.each(props || [], (it) => {
            if (dsf.hasOwn(this.$props, it)) {
              attrs[it] = this.$props[it];
            }
          })
          if (designMetaDataNode.exclude) {
            _.each(designMetaDataNode.exclude || [], (it) => {
              delete attrs[it];
            });
          }
          this.metadata.content = !dsf.isEmptyObject(attrs) ? JSON.stringify(attrs) : "{}";
        }
        return {
          self: this,
          init: null,
          metadata: this.metadata
        };
      }
    },
    dataPropKey: "value"
  }
};

function staticEditor() {
  let _this = this;
  this.$openDialog({
    title: "数据维护",
    content: "DesStaticDataFormItem",
    width: "70vw",
    height: "60vh",
    // overflow: "hidden hidden",
    params: {
      designer: this.$designer,
      value: _this.staticValue || [],
      owner: this
    },
    btns: [
      {
        text: "确定",
        handler(res) {
          let data = res?.yes();
          if (_this.caption && data) {
            let vas = _this.metadata.valueAttributes;
            if (vas.length > 0) {
              if (vas.length == 1) {
                let va = vas[0]
                let value = data[va.code];
                _this.staticValue = value
              }
              else {
                _this.staticValue = data
              }
            }
          }
        }
      },
      {
        text: "清除",
        handler() {
          _this.staticValue = null;
        }
      },
      {
        text: "取消"
      }
    ]
  });
}

